import React from "react";
import Head from "next/head";
import { ThemeProvider } from "styled-components";

import Navigation from "sections/Navigation";
import BannerTwo from 'sections/BannerTwo';
import Service from "sections/Service";
import CoinFund from "sections/CoinFund";
import About from "sections/About";
import Awards from "sections/Awards";
import UserMap from "sections/UserMap";
import Wallet from "sections/Wallet";
import Statistics from "sections/Statistics";
import Stack from "sections/Stack"; 
import RoadMap  from "sections/RoadMap";
import Faq from "sections/Faq";
import Footer from "sections/Footer";

// import FavIcon from "assets/images/fav-icon.png";
import FavIcon from "assets/images/logo-white.png";
import theme from "assets/theme/theme";
import GlobalStyle from "assets/theme";

const Home = () => {
  return (
    <ThemeProvider theme={theme}>
      <Head>
      <title>Panache | Your Lifestyle Token</title>
        <meta name="Description" content="Lifestyle marketplace, that will list vendors and allow customers to trade using panache token and earn panache" />
        <meta name="Keywords" content="premium fashion, premium jewelleries, top jeweller, top models, top wrist watch, top designers in amaerica, top footwears, top cars" />
        <meta name="theme-color" content="#280D57" />
        <link rel="shortcut icon" type="image/x-icon" href={FavIcon} />
      </Head>

      <GlobalStyle />
      <Navigation />
      <BannerTwo />
      <Service />
      <CoinFund dayCount />
      <About />
      <Awards />
      {/* <UserMap /> */}
      <Wallet />
      <RoadMap/>
      {/* <Statistics /> */}
       {/* <Faq /> */}
      <Stack />
      <Footer />
    </ThemeProvider>
  );
};

export default Home;